import React, {useEffect, useState} from 'react';
import {Button, CardImg, Container} from "react-bootstrap";
import './home.css';
import {createRoutesFromChildren, useNavigate} from "react-router-dom";
import database from "../../firebase";
import {onValue, ref, get, child} from "firebase/database";

const Home = () => {
    useEffect(() => {
        const dbRef = ref(database);
        get(child(dbRef, 'event')).then((snapshot) => {
            console.log(snapshot.val());
        })
    }, [])
    const navigate = useNavigate();
    const [isMessageShow, setIsMessageShow] = useState(false);
    return (
        <Container fluid className='h-100 p-0'>
            <div className='d-flex  justify-content-center align-items-center h-100'
                 style={!isMessageShow ? {backgroundImage : `url(${process.env.PUBLIC_URL}/background.webp)`, backgroundSize : 'cover', } :
                     {backgroundImage : `url(${process.env.PUBLIC_URL}/background-page1.png)`, backgroundSize : 'cover', backgroundPosition : 'bottom', backgroundRepeat : 'no-repeat'}}>
                {
                    isMessageShow
                        ?
                    <CardImg src={process.env.PUBLIC_URL + '/envelope-with-love.png'} className='envelope' onClick={() => setIsMessageShow(true)}/>
                        :
                    <div className='container-letter'>
                            <div className='custom-paragraph'>
                                <p>Nhn dp dng sinh, v li ha tng N 1 mn qu sau thi xong. Ti c t lm 1 trang web nh nh, y l tm huyt ca ti trong 2 tun qua, hi vng B N thch .</p>
                                <p>Hng dn: Sau khi N click vo  Next bn di, Ti c  nhng mn qu b mt bn trong cc hp qu c treo trn cy thng. N ch c chn 1 trong nhng hp qu . Phn qu bt ng s c gi cho N sau! Chc N may mn nh!</p>
                            </div>
                        <div className='d-flex justify-content-end'>
                            <Button className='btn m-3' size='lg' variant='primary'
                                    onClick={() => navigate('/receive-gift')}>Next</Button>
                        </div>
                    </div>


                }

            </div>
        </Container>
    );
};

export default Home;